body {
  font-family: Authentic, sans-serif;
  font-size: 12px;
}

.pagedjs_page_content {
  orphans: 2;
  widows: 2;
}

h2 {
  font-family: SolideMirageEtroit, sans-serif;
  font-size: 72px;
}

h3 {
  font-family: Authentic, sans-serif;
  text-align: center;
}
h3:first-child {
  margin-top: 0;
  padding-top: 0;
}

blockquote {
  margin-left: 1cm;
  margin-right: 1cm;
}
blockquote > p {
  margin: 0;
  padding: 0;
  text-indent: 0;
}

sup,
.footnote {
  font-family: Authentic, sans-serif;
  vertical-align: top;
  position: relative;
  top: -0.2em;
}
sup:after,
.footnote:after {
  font-variant-position: initial;
}

.full-spread-image-caption,
.thumbcaption {
  font-family: Authentic, sans-serif;
  text-align: center;
}

.full-spread-image-caption {
  line-height: 8pt;
  text-align: left;
}

/* we have to disable the baseline grid margin/padding 
that Plumber sets above for the first element on a page */
.chapter > p:first-child {
  margin-top: 0;
  padding-top: 0;
}

@media screen {
  /* 
  For debugging the baseline grid append ?grid to the url 
  */
  .grid .pagedjs_page_content {
    background-image: repeating-linear-gradient(to bottom, transparent 0px, transparent 11.25pt, rgba(255, 0, 0, 0.1) 11.25pt, rgba(255, 0, 0, 0.1) 12pt);
  }
}
h2 {
  page-break-before: left;
}

.author {
  page-break-after: always;
}

.title-page {
  page-break-before: right;
}

.empty-left-page {
  page-break-before: left;
}

.full-spread-image-section {
  page-break-before: left;
}

.full-page-image {
  page-break-before: always;
}

.full-page-image.full-page-image-left {
  page-break-before: left;
}

.full-page-image.full-page-image-right {
  page-break-after: always;
}

.full-page-image {
  width: var(--pagedjs-width);
  height: var(--pagedjs-height);
  overflow: hidden;
  left: calc((var(--pagedjs-bleed-left) + var(--pagedjs-margin-left)) * -1);
  top: calc((var(--pagedjs-bleed-top) + var(--pagedjs-margin-top)) * -1);
  position: absolute;
}

.full-page-image div {
  width: calc(var(--pagedjs-width) * 2 - var(--pagedjs-bleed-left) - var(--pagedjs-bleed-right));
  height: var(--pagedjs-height);
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

/* compensate for bleeds */
.pagedjs_right_page .full-page-image div {
  margin-left: calc(-1 * var(--pagedjs-width) + var(--pagedjs-bleed-left) + var(--pagedjs-bleed-right));
}

.full-page-image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  margin: 0 !important;
}

.title-spread .full-page-image {
  background-color: black;
}

.full-spread-image-section .full-page-image div.full-spread-image-caption {
  height: auto;
  position: absolute;
  display: block;
  right: calc(var(--pagedjs-bleed-right) + var(--pagedjs-margin-right));
  bottom: calc(var(--pagedjs-bleed-bottom) + var(--pagedjs-margin-bottom));
  width: calc(var(--pagedjs-width) - var(--pagedjs-bleed-right) - var(--pagedjs-margin-right) - var(--pagedjs-bleed-left) - var(--pagedjs-margin-left));
}

/* General styling */
html {
  font-size: 62.5%;
}

body {
  color: var(--color-main);
  margin: 0;
  padding: 0;
}

img {
  width: auto;
  height: auto;
  max-width: 85%;
  max-height: 80%;
  border-radius: 1rem;
  display: block;
  margin: 12pt auto;
}

h3 + p {
  text-indent: 0 !important;
}

.pagedjs_pages .chapter.invert-type h2 + p {
  text-indent: 0 !important;
}

/*
Setting named-pages
https://pagedjs.org/documentation/8-named-page/
*/
.empty-page {
  page: empty-page !important;
}

/* Prevent superscript from messing up the line-height */
sup {
  vertical-align: top;
  position: relative;
  top: -0.5em;
}

a {
  text-decoration: none;
  color: black;
  overflow-wrap: break-word;
  word-wrap: break-word;
  word-break: break-word;
}

h2 {
  overflow-wrap: break-word;
  word-wrap: break-word;
  word-break: break-word;
}

/* Generic css */
/* Show url of a tags */
/* a::after {
content: " (" attr(href) ")";
} */
a.image {
  display: block;
  text-align: center;
}

.pagedjs_pages .pagedjs_area .chapter .image-container {
  margin: 0;
  padding: 0;
  text-indent: 0;
  text-align: center;
}
.pagedjs_pages .pagedjs_area .chapter .image-container a {
  display: block;
}

/* 
	Change the display of these margin elements to inline-block
	so they have a meaningful width for use with the P5js sketch.
*/
.pagedjs_pages .pagedjs_margin-top-center .pagedjs_margin-content,
.pagedjs_pages .pagedjs_margin-bottom-center .pagedjs_margin-content {
  display: inline-block;
  width: auto !important;
  margin: 0 auto;
}

/* For placement op p5js sketch relative to the page */
.pagedjs_page, .pagedjs_pagebox {
  position: relative;
}

/* thumbnails, keep caption and image on same page */
.thumb {
  break-inside: avoid;
}

.thumbinner {
  margin: 0 auto;
}

/* mediawiki gives thumbs an inline fixed width */
.thumbinner.image-container {
  width: auto !important;
}